@import '../../variables.scss';

.navbar {
    margin-top: 10px;
    height: 70px;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .item {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
        }

        .left {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 25px;
        }

        .center {
            font-size: 30px;
            letter-spacing: 2px;
        }

        .right {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 25px;

            .icons {
                display: flex;
                gap: 15px;
                color: #777;
                cursor: pointer;

                .cart-icon {
                    position: relative;

                    span {
                        font-size: 12px;
                        width: 20px;
                        height: 20px;
                        border-radius: 50%;
                        background-color: $main-color;
                        color: white;
                        position: absolute;
                        right: -10px;
                        top: -10px;

                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
            }
        }
    }
}