@import '../../variables.scss';

.cart {
    position: absolute;
    right: 20px;
    top: 80px;
    z-index: 999;
    background-color: white;
    padding: 20px;
    -webkit-box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.2);
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.2);


    h1 {
        margin-bottom: 30px;
        color: $main-color-button-hover-dark;
        font-weight: 400;
        font-size: 24px;
    }

    .item {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-bottom: 30px;

        img {
            height: 100px;
            width: 80px;
            object-fit: cover;
        }
    }

    .details {
        h1 {
            color: $main-color;
            font-size: 18px;
            font-weight: 500;
        }

        p {
            color: gray;
            margin-bottom: 10px;
            font-size: 14px;
        }

        .price {
            color: $main-color;
        }
    }

    .delete {
        color: red;
        font-size: 30px;
        cursor: pointer;
    }

    .total {
        display: flex;
        justify-content: space-between;
        font-weight: 500;
        font-size: 18px;
        margin-bottom: 20px;
    }

    .button {
        width: 250px;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        cursor: pointer;
        border: none;
        background-color: $main-color;
        color: white;
        font-weight: 500;
        margin-bottom: 20px;
    }

    .disabled-button {
        opacity: 60%;
        cursor: not-allowed!important;
    }

    .reset {
        color: red;
        font-size: 12px;
        cursor: pointer;
    }
}